body {
    font-family: 'Lato', sans-serif;
    font-size: 14px;
    line-height: 17px;
    font-style: normal;



    padding: 0;
    margin: 0; }

a {
    text-decoration: none;
    color:#000 {} }


header {
    background: url(./img/Bg.png) no-repeat;

    background-size: cover;


    position: relative;

    width: 100%;
    height: 100vh; }





.header_navigation {
    height: 120px;

    align-items: center;


    & a {
        color:#fff {}
        text-transform: uppercase; }

    &__logo {
        width: 175px;
        height: 30px;

        display: flex; }


    &__menu {
        display: flex;
        justify-content: space-between;
        align-items: center; }

    &__menu a {
        text-transform: uppercase;

        margin: 0 10px; }

    &__login {
        border: 1px solid #ffffff;
        border-radius: 6px;

        padding: 10px 30px; }

    &__signupBlock {
        display: flex;
        justify-content: flex-end;
        align-items: center; }

    &__signupBlock a {

        & img {
            margin-right: 10px; } } }

.header_info {
    color:#ffff {}


    position: absolute;
    transform: translate(0,-50%);
    top: 50%;
    left: 0;
    right: 0;
    margin: 0 auto;

    display: flex;
    flex-direction: column;
    align-items: center;


    width: 27%;


    & h1, & p, {
        margin-bottom: 50px; }
    & h1 {
        line-height: 86px;
        font-size: 72px; }
    & p {
        font-size: 24px;
        font-weight: 600;
        line-height: 150%;
        text-align: center; }

    & a {
        background: #ffffff;
        padding: 20px 50px;
        border-radius: 6px;

        color:transparent {}
        text-transform: uppercase; } }


.under_header_info {
    height: 500px;
    display: flex;
    align-items: center;

    &__block1 {

        & h2 {
            font-weight: 600;
            font-size: 36px;
            line-height: 43px;

            margin-bottom: 30px; }
        & p {
            text-align: justify;
            margin-bottom: 20px; }
        & a {
            color: #ED4C5C;
            text-align: justify; } }

    &__block2 {
        display: flex;
        justify-content: center;

        position: relative;

        & a {
           position: absolute;
           top: 40%;
           left: 30px; } } }





